const MainPage = `
mixin message(img, text, time)
  li.chart__message
    if img
      img.chart__img(src="../..sefs/../avatar.svg" alt="  ")
    p.chart__text= text
    span.chart__time= time

mixin contact(name, message, time, count)
  li.sidebar__listItem
    img.sidebar__userImage(src="../images/avatar.svg", alt=" ")
    .sidebar__userSection
      h4.sidebar__userName= name
      p.sidebar__userMassage= message
    .sidebar__massageAttr
      p.siadebar__time= time
      if count
        .sidebar__countNewMessage= count

main.content
  aside.sidebar
    a.sidebar__profileLink(href="../profile") 
      span.material-icons.sidebar__profileLink_icon chevron_right
    input.sidebar__searchInput(type="text" placeholder="" name="search")
    ul.sidebar__list
      +contact("", "", "10:22", "2")
      +contact("", " ", "10:25", "3")
      +contact("", " ", "10:25", "1")
      +contact("", " ", "10:25", "2")
      +contact("", " ", "10:25", "3")
      +contact("", " ", "10:25", "3")
      +contact("", " ", "10:25", "5")
      +contact("", " ", "10:25",)
      +contact("", " ", "10:25",)
      +contact("", " ", "10:25",)
      +contact("", " ", "10:25",)
      +contact("", " ", "10:25",)
      +contact("", " ", "10:25",)
      +contact("", " ", "10:25",)
      +contact("", " ", "10:25",)
      +contact("", " ", "10:25",)
      +contact("", " ", "",)
      +contact("", " ", "",)
      +contact("", " ", "10:25",)
  .chart
    //- h3.chart_empty     
    .chart__header
      img.chart__avatar(src="avatar.svg", alt=" ")
      h4.chart__username Boriss
      button.chart__buttonMore
    ul.chart__meetingRoom
      h4.chart__massDate 10 
      +message("./images/mess-pic.png", "", "10:55")
      +message("", "! ,           -     ", "10:55")
      +message("", "! ,           -     ", "10:55")
    .chart__footer
      button.chart__addButton 
      input.chart__inputMessage(type="text" name="message")
      button.chart__sendButton
`

export default MainPage
