const Profile = `.page
      aside.profile__back
        a.profile__backButton(href="../MainPage/index.pug")
      section.register
        button.profile__avatar(type="button")
        h2.profile__title 
        form.register__form
          fieldset.register__fields
            p.register__input-name 
            input.register__input(
              type="email"
              name="email"
              pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$"
              required
              placeholder="pochta@pochta.com"
              disabled
            )
            span.register__error

            p.register__input-name 
            input.register__input(
              type="text"
              name="login"
              required
              minLength="2"
              placeholder="Mylogin"
              disabled
            )
            span.register__error

            p.register__input-name 
            input.register__input(
              type="text"
              name="name"
              required
              placeholder=""
              disabled

            )
            span.register__error

            p.register__input-name 
            input.register__input(
              type="text"
              name="lastname"
              required
              placeholder=""
              disabled
            )
            span.register__error

            p.register__input-name   
            input.register__input(
              type="text"
              name="display_name"
              required
              placeholder=""
              disabled
            )
            span.register__error

            p.register__input-name 
            input.register__input(
              type="tel"
              name="phone"
              required
              placeholder="+79111111111"
              disabled 
            )
            span.register__error

            p.register__input-name 
            input.register__input(
              type="password"
              name="password"
              required
              minLength="8"
              disabled 
            )
            span.register__error

          span.register__submit-error
          button.register__submit-button(type="submit")  
          button.register__submit-button(type="submit")   
          a.register__subtitle(href="#") `

export default Profile